.features__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

@media (min-width: 768px) {
  .features__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 0px 130px;
  }
}

@media (min-width: 1440px) {
  .features__list {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-gap: 10px 88px;
  }
}

.features__title {
  font-size: 36px;
  line-height: 40px;
  font-family: "Oswald", "Arial", sans-serif;
  font-weight: 400;
}

@media (min-width: 768px) {
  .features__title {
    font-size: 60px;
    line-height: 60px;
  }
}

.features__item {
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: @dark-grey-100;
}

@media (min-width: 768px) {
  .features__item {
    font-size: 16px;
    line-height: 24px;
  }
}
